  border-radius: 4px;
  height: 36px;
}

.el-radio--medium.is-bordered .el-radio__label {
  font-size: 14px;
}

.el-radio--medium.is-bordered .el-radio__inner {
  height: 14px;
  width: 14px;
}

.el-radio--small.is-bordered {
  padding: 8px 15px 0 10px;
  border-radius: 3px;
  height: 32px;
}

.el-radio--small.is-bordered .el-radio__label {
  font-size: 12px;
}

.el-radio--small.is-bordered .el-radio__inner {
  height: 12px;
  width: 12px;
}

.el-radio--mini.is-bordered {
  padding: 6px 15px 0 10px;
  border-radius: 3px;
  height: 28px;
}

.el-radio--mini.is-bordered .el-radio__label {
  font-size: 12px;
}

.el-radio--mini.is-bordered .el-radio__inner {
  height: 12px;
  width: 12px;
}

.el-radio + .el-radio {
  margin-left: 30px;
}

.el-radio__input {
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  display: inline-block;
  line-height: 1;
  position: relative;
  vertical-align: middle;
}

.el-radio__input.is-disabled .el-radio__inner {
  background-color: #f5f7fa;
  border-color: #e4e7ed;
  cursor: not-allowed;
}

.el-radio__input.is-disabled .el-radio__inner::after {
  cursor: not-allowed;
  background-color: #f5f7fa;
}

.el-radio__input.is-disabled .el-radio__inner + .el-radio__label {
  cursor: not-allowed;
}

.el-radio__input.is-disabled.is-checked .el-radio__inner {
  background-color: #f5f7fa;
  border-color: #e4e7ed;
}

.el-radio__input.is-disabled.is-checked .el-radio__inner::after {
  background-color: #c0c4cc;
}

.el-radio__input.is-disabled + span.el-radio__label {
  color: #c0c4cc;
  cursor: not-allowed;
}

.el-radio__input.is-checked .el-radio__inner {
  border-color: #409EFF;
  background: #409EFF;
}

.el-radio__input.is-checked .el-radio__inner::after {
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
}

.el-radio__input.is-checked + .el-radio__label {
  color: #409EFF;
}

.el-radio__input.is-focus .el-radio__inner {
  border-color: #409EFF;
}

.el-radio__inner {
  border: 1px solid #dcdfe6;
  border-radius: 100%;
  width: 14px;
  height: 14px;
  background-color: #fff;
  position: relative;
  cursor: pointer;
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.el-radio__inner:hover {
  border-color: #409EFF;
}

.el-radio__inner::after {
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: #fff;
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
          transform: translate(-50%, -50%) scale(0);
  -webkit-transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6), -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
}

.el-radio__original {
  opacity: 0;
  outline: none;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
}

.el-radio:focus:not(.is-focus):not(:active) {
  /*获得焦点时 样式提醒*/
}

.el-radio:focus:not(.is-focus):not(:active) .el-radio__inner {
  -webkit-box-shadow: 0 0 2px 2px #409EFF;
          box-shadow: 0 0 2px 2px #409EFF;
}

.el-radio__label {
  font-size: 14px;
  padding-left: 10px;
}

