.el-checkbox__inner:hover {
  border-color: #409EFF;
}

.el-checkbox__inner::after {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  content: "";
  border: 1px solid #fff;
  border-left: 0;
  border-top: 0;
  height: 7px;
  left: 4px;
  position: absolute;
  top: 1px;
  -webkit-transform: rotate(45deg) scaleY(0);
