  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  bottom: auto;
  width: 126px;
}

.el-upload-list--picture-card .el-progress .el-progress__text {
  top: 50%;
}

.el-upload-list--picture .el-upload-list__item {
  overflow: hidden;
