}

.el-upload-cover__interact .btn:hover span {
  opacity: 1;
}

.el-upload-cover__interact .btn i {
  color: #fff;
  display: block;
  font-size: 24px;
  line-height: inherit;
  margin: 0 auto 5px;
}

.el-upload-cover__title {
  position: absolute;
  bottom: 0;
